import { Image } from "antd"
import React from "react"
import s from './UserPhoto.module.scss'
import userPhoto from '../../../images/img.jpg'

type PropsType = {
    size: string
}

export const UserPhoto: React.FC<PropsType> = React.memo(({size}) => {
    return (
        <div className={s.userPhoto + ' ' + s[size]}>
            <Image src={userPhoto} />
        </div>
    )
})